import React, {Component} from "react";
import {connect} from "react-redux";
import {getSelectedProcessTypeValue} from "eshop/modules/configurator/selectors/filters";

class OraMobileOffersInfoSectionComponent extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        var processType = this.props.process;
        var htmlContext = this.props.infoSections[processType];

        return (
        <div>
            {
                (this.props.descriptions.headerValue)  ?
                    <div dangerouslySetInnerHTML={ {__html: this.props.descriptions.headerValue} } />
                :
                    null
            }
            <div dangerouslySetInnerHTML={ {__html: htmlContext} } />
        </div>
        )
    }
}

const mapStateToProps = state => ({
    process: getSelectedProcessTypeValue(state)
})

const mapDispatchToProps = dispatch => ({

})

export default connect(mapStateToProps, mapDispatchToProps)(OraMobileOffersInfoSectionComponent);
